<template>
    <div>
        <div class="search-content">
            <!-- search-top-->
            <div class="search-top">
                <div class="search-top-close" @click="$router.go(-1)"></div>
                <div class="search-top-wrap">
                    <div class="wrap-left">
                        <input type="text" class="search" placeholder="输入商品名称" v-model="keywords"/>
                    </div>
                    <div class="wrap-right" @click="goKeywords()"></div>
                </div>
            </div>
            <!-- search-main-->
            <div class="search-main">
                <div class="search-main-title">
                    <div class="title-text">最近搜索</div>
                    <div class="title-icon" @click="clearKeywords()"></div>
                </div>
                <div class="search-main-wrap">
                    <div class="main-wrap-keywords" v-for="(item,index) in keywordsData" :key="index" @click="goKeywords(item)">{{item}}</div>
                </div>
            </div>
            <!--热门搜索-->
            <div class="search-main">
                <div class="search-main-title">
                    <div class="title-text">热门搜索</div>

                </div>
                <div class="search-main-wrap">
                    <div class="main-wrap-keywords" v-for="(item,index) in hotkeywords" :key="index" @click="goKeywords(item.title)">{{item.title}}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState,mapMutations,mapActions} from "vuex"
    import { Dialog } from 'vant';
    export default {
        data() {
            return {
                //搜索关键词
                keywords: ""
            }
        },
        created() {
            //网页标签名改变
            document.title = this.$route.meta.title;
            //存储搜索关键词的数组
            this.keywordsArray =this.keywordsData?this.keywordsData:[];
            //运行hotKeywords方法
            this.hotKeywords();
        },
        computed:{
            //mapState
            ...mapState({
                //获取关键词数据
                keywordsData:(state)=>state.search.keywordsData,
                //获取热门关键词
                hotkeywords:(state)=>state.search.hotkeywords
            })
        },
        methods: {
            //获取同步方法
            ...mapMutations({
                //获取同步方法的关键词数据方法
                SET_KEYWORDS:"search/SET_KEYWORDS",
                //清空关键词方法
                CLEAR_KEYWORD:"search/CLEAR_KEYWORD"
            }),
            //获取异步方法
            ...mapActions({
                hotKeywords:"search/hotKeywords"
            }),
            //清空关键词clearKeywords
            clearKeywords(){
                if (this.keywordsData.length>0){
                    Dialog.confirm({
                        title: '',
                        message: '确认要清除吗?',
                    })
                        .then(() => {
                            this.CLEAR_KEYWORD();
                        })
                        .catch(() => {

                        });
                }

            },
            //关键词运行方法 储存数组对象 到最近搜索
            goKeywords(keywords) {
                let tmpKeywords = keywords ||this.keywords ||"";
                if (tmpKeywords) {
                    if (this.keywordsArray.length > 0) {
                        for (let i = 0; i < this.keywordsArray.length; i++) {
                            if (this.keywordsArray[i] === tmpKeywords) {
                                this.keywordsArray.splice(i--, 1);
                            }

                        }
                    }
                    this.keywordsArray.unshift(tmpKeywords);
                    this.SET_KEYWORDS({keywordsData:this.keywordsArray})
                }
                this.$router.push('/goods/search/content?kwords='+tmpKeywords)
            }
        }
    }
</script>

<style scoped>
    .search-content {
        position: fixed;
        z-index: 99;
        width: 100%;
        height: 100%;
        background: #FFF;

    }

    /*.search-content .search-top*/
    .search-content .search-top {
        display: flex;
        align-items: center;
        box-sizing: border-box;
        width: 100%;
        height: 1rem;
        padding: 0 .2rem;
        border-bottom: 1px solid #efefef;
    }

    .search-content .search-top .search-top-close {
        width: .5rem;
        height: .5rem;
        background: url("../../assets/images/home/index/close.png") no-repeat;
        background-size: 100%;
        background-position: center;
        margin-right: .2rem;
    }

    .search-content .search-top .search-top-wrap {
        display: flex;
        align-items: center;
        border-radius: .1rem;
        width: 78%;
        height: .64rem;
        border: 1px solid #b2b2b2;
        border-radius: .1rem;
    }

    .search-top-wrap .wrap-left {
        width: 85%;
        height: 100%;
        border-right: 1px solid #b2b2b2;
    }

    .search-top-wrap .wrap-left .search {
        width: 80%;
        height: 100%;
        margin-left: .2rem;
        font-size: .28rem;
        outline: none;
        background: 0;
        border: none;
        box-sizing: border-box;
        padding: 0 .1rem;

    }

    .search-top-wrap .wrap-right {
        width: .5rem;
        height: .5rem;
        background: url("../../assets/images/home/index/search-icon.png") no-repeat;
        background-size: 100%;
        background-position: center;

        margin-left: .15rem;
    }

    /*.search-content .search-main*/
    .search-main {
        width: 100%;
        margin-top: .2rem;
    }

    .search-main .search-main-title {
        display: flex;
        width: auto;
        justify-content: space-between;
        align-items: center;
        box-sizing: border-box;
        padding-left: .4rem;
        padding-right: .4rem;
    }

    .search-main .search-main-title .title-text {
        width: auto;
        font-size: .28rem;
    }

    .search-main .search-main-title .title-icon {
        width: .4rem;
        height: .4rem;
        background: url("../../assets/images/home/index/title-icon.png") no-repeat;
        background-size: 100%;
        background-position: center;

    }

    .search-main-wrap {
        display: flex;
        width: auto;
        justify-content: flex-start;
        padding-left: .4rem;
        padding-right: .4rem;
        margin-top: .3rem;
        flex-wrap: wrap;
    }

    .search-main-wrap .main-wrap-keywords {
        width: 26%;
        height: .6rem;
        color: #717376;
        border: 1px solid #efefef;
        border-radius: .64rem;
        font-size: .28rem;
        text-align: center;
        overflow: hidden;
        line-height: .6rem;
        margin-right: 1.3%;
        margin-left: 1.3%;
        margin-bottom: .2rem;
        padding-left: .1rem;
        padding-right: .1rem;
    }
</style>